<template>
  <div class="dashboard">
    <!-- 统计卡片区域 -->
    <section class="stats-section">
      <div class="section-title">
        <h2>数据概览</h2>
      </div>
      
      <div class="stats-grid">
        <div class="stats-card" @click="navigateTo('/shops')">
          <div class="stats-icon">
            <el-icon><Shop /></el-icon>
          </div>
          <div class="stats-content">
            <div class="stats-value">{{ shopCount }}</div>
            <div class="stats-label">店铺总数</div>
          </div>
        </div>
        
        <div class="stats-card" @click="navigateTo('/images')">
          <div class="stats-icon">
            <el-icon><Picture /></el-icon>
          </div>
          <div class="stats-content">
            <div class="stats-value">{{ imageCount }}</div>
            <div class="stats-label">图片总数</div>
          </div>
        </div>
        
        <div class="stats-card">
          <div class="stats-icon">
            <el-icon><Goods /></el-icon>
          </div>
          <div class="stats-content">
            <div class="stats-value">{{ publishedCount }}</div>
            <div class="stats-label">已发布商品</div>
          </div>
        </div>
      </div>
    </section>

    <!-- 授权店铺展示区域 -->
    <section class="shops-section">
      <div class="section-header">
        <div class="section-title">
          <h2>授权店铺</h2>
        </div>
        <el-button type="primary" @click="refreshAuthShops" :loading="shopLoading">
          <el-icon><Refresh /></el-icon>
          <span>刷新</span>
        </el-button>
      </div>
      
      <div class="shops-content" v-loading="shopLoading">
        <div v-if="!shopLoading && !authorizedShops.length" class="empty-state">
          <el-empty description="暂无授权店铺，请在店铺管理页面查询店铺获取授权信息">
            <el-button type="primary" @click="navigateTo('/shops')">前往店铺管理</el-button>
          </el-empty>
        </div>
        
        <el-table 
          v-else-if="!shopLoading && authorizedShops.length > 0" 
          :data="authorizedShops" 
          style="width: 100%"
          :stripe="true"
          :border="false"
          class="apple-table"
        >
          <el-table-column prop="shop_name" label="店铺名称" min-width="120"></el-table-column>
          <el-table-column prop="user_name" label="会员名" min-width="120"></el-table-column>
          <el-table-column prop="user_nick" label="昵称" min-width="120"></el-table-column>
          <el-table-column prop="is_pro" label="鱼小铺">
            <template #default="scope">
              <el-tag :type="scope.row.is_pro ? 'success' : 'info'" effect="plain">
                {{ scope.row.is_pro ? '已开通' : '未开通' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="is_valid" label="订购状态">
            <template #default="scope">
              <el-tag :type="scope.row.is_valid ? 'success' : 'danger'" effect="plain">
                {{ scope.row.is_valid ? '有效' : '无效' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="valid_end_time" label="有效期">
            <template #default="scope">
              {{ formatTimestamp(scope.row.valid_end_time) }}
            </template>
          </el-table-column>
        </el-table>
      </div>
    </section>

    <div class="dashboard-grid">
      <section class="logs-section">
        <div class="section-title">
          <h2>最近发布记录</h2>
        </div>
        
        <div class="logs-content">
          <el-table 
            :data="recentLogs" 
            style="width: 100%" 
            v-loading="loading"
            :stripe="true"
            :border="false"
            class="apple-table"
          >
            <el-table-column prop="title" label="商品标题" min-width="180" show-overflow-tooltip></el-table-column>
            <el-table-column prop="price" label="价格" width="100"></el-table-column>
            <el-table-column prop="created_at" label="发布时间" width="180">
              <template #default="scope">
                {{ formatDate(scope.row.created_at) }}
              </template>
            </el-table-column>
            <el-table-column prop="status" label="状态" width="120">
              <template #default="scope">
                <el-tag :type="getStatusType(scope.row.status)" effect="plain">
                  {{ scope.row.status }}
                </el-tag>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </section>
      
      <section class="chart-section">
        <div class="section-title">
          <h2>近期发布统计</h2>
        </div>
        
        <div class="chart-content">
          <div class="placeholder-chart">
            <el-empty description="数据统计功能正在开发中">
              <template #image>
                <el-icon><DataAnalysis /></el-icon>
              </template>
            </el-empty>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { dashboardAPI } from '../utils/api'
import { ElMessage } from 'element-plus'
import { 
  Shop, 
  Picture, 
  Goods, 
  Refresh,
  DataAnalysis
} from '@element-plus/icons-vue'
import { shopAPI } from '../utils/api' // Added import for shopAPI

export default {
  name: 'DashboardPage',
  components: {
    Shop,
    Picture,
    Goods,
    Refresh,
    DataAnalysis
  },
  setup() {
    const router = useRouter()
    const shopCount = ref(0)
    const imageCount = ref(0)
    const publishedCount = ref(0)
    const recentLogs = ref([])
    const loading = ref(false)
    const shopLoading = ref(false) // 新增店铺加载状态
    const authorizedShops = ref([])

    const fetchDashboardData = async () => {
      loading.value = true
      try {
        // 调用真实API获取仪表盘数据
        const response = await dashboardAPI.getStats()
        const { data } = response.data
        
        // 更新状态
        shopCount.value = data.shopCount || 0
        imageCount.value = data.imageCount || 0
        publishedCount.value = data.publishedCount || 0
        recentLogs.value = Array.isArray(data.recentLogs) ? data.recentLogs : []
      } catch (error) {
        console.error('获取仪表盘数据失败:', error)
        ElMessage.error('获取仪表盘数据失败，请稍后重试')
        // 重置数据
        shopCount.value = 0
        imageCount.value = 0
        publishedCount.value = 0
        recentLogs.value = []
      } finally {
        loading.value = false
      }
    }

    const loadAuthorizedShops = async () => {
      try {
        // 获取所有店铺
        shopLoading.value = true // 开始加载
        const response = await shopAPI.getShops()
        const allShops = response.data.data
        
        // 存储授权店铺的数组
        authorizedShops.value = []
        
        // 如果没有店铺，直接返回
        if (!allShops || allShops.length === 0) {
          return
        }
        
        // 遍历所有店铺，查询授权状态
        for (const shop of allShops) {
          try {
            // 获取店铺授权信息
            const authResponse = await shopAPI.queryShopInfo(shop.id)
            if (authResponse.data && authResponse.data.data && authResponse.data.data.shops) {
              // 将授权店铺添加到数组
              authorizedShops.value = authorizedShops.value.concat(authResponse.data.data.shops)
            }
          } catch (error) {
            console.error(`获取店铺${shop.id}的授权信息失败:`, error)
          }
        }
      } catch (error) {
        console.error('加载授权店铺数据失败:', error)
        authorizedShops.value = []
        ElMessage.error('获取授权店铺数据失败')
      } finally {
        shopLoading.value = false // 结束加载
      }
    }

    const refreshAuthShops = async () => {
      try {
        await loadAuthorizedShops()
        ElMessage.success('授权店铺数据已刷新')
      } catch (error) {
        ElMessage.error('刷新授权店铺数据失败')
      }
    }

    const formatTimestamp = (timestamp) => {
      if (!timestamp || timestamp === 0) return '无'
      const date = new Date(timestamp * 1000)
      return date.toLocaleDateString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
      })
    }

    const getStatusType = (status) => {
      if (status === 'published') return 'success'
      if (status === 'failed') return 'danger'
      return 'info'
    }
    
    const formatDate = (dateString) => {
      if (!dateString) return ''
      const date = new Date(dateString)
      return date.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      })
    }
    
    const navigateTo = (path) => {
      router.push(path)
    }

    onMounted(() => {
      fetchDashboardData()
      loadAuthorizedShops()
    })

    return {
      shopCount,
      imageCount,
      publishedCount,
      recentLogs,
      loading,
      shopLoading, // 添加到返回值
      authorizedShops,
      getStatusType,
      formatDate,
      formatTimestamp,
      refreshAuthShops,
      navigateTo
    }
  }
}
</script>

<style scoped>
.dashboard {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.section-title h2 {
  font-size: 22px;
  font-weight: 500;
  color: var(--text-primary);
  margin: 0 0 16px 0;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

/* 统计卡片样式 */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 20px;
}

.stats-card {
  background-color: var(--background-primary);
  border-radius: 12px;
  padding: 24px;
  display: flex;
  align-items: center;
  box-shadow: var(--card-shadow);
  transition: var(--transition-default);
  cursor: pointer;
}

.stats-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
}

.stats-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: var(--primary-color);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 16px;
}

.stats-icon .el-icon {
  font-size: 24px;
  color: white;
}

.stats-content {
  flex: 1;
}

.stats-value {
  font-size: 28px;
  font-weight: 600;
  color: var(--text-primary);
  line-height: 1.2;
}

.stats-label {
  font-size: 14px;
  color: var(--text-secondary);
  margin-top: 4px;
}

/* 店铺区域样式 */
.shops-section, .logs-section, .chart-section {
  background-color: var(--background-primary);
  border-radius: 12px;
  padding: 24px;
  box-shadow: var(--card-shadow);
}

.shops-content, .logs-content {
  margin-top: 10px;
}

/* 表格样式优化 */
.apple-table {
  border-radius: 8px;
  overflow: hidden;
}

:deep(.el-table th) {
  background-color: var(--background-secondary);
  border-bottom: 1px solid var(--border-color);
  color: var(--text-secondary);
  font-weight: 500;
}

:deep(.el-table td) {
  border-bottom: 1px solid var(--border-color);
}

:deep(.el-table--striped .el-table__body tr.el-table__row--striped td) {
  background-color: var(--background-tertiary);
}

:deep(.el-table--enable-row-hover .el-table__body tr:hover > td) {
  background-color: var(--background-secondary);
}

.empty-state {
  padding: 40px 0;
}

/* 网格布局 */
.dashboard-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
}

.logs-section, .chart-section {
  height: 380px;
  display: flex;
  flex-direction: column;
}

.logs-content, .chart-content {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.logs-content .el-table {
  flex: 1;
}

.placeholder-chart {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.placeholder-chart .el-icon {
  font-size: 60px;
  color: var(--primary-color);
}

/* 响应式设计 */
@media screen and (max-width: 992px) {
  .dashboard-grid {
    grid-template-columns: 1fr;
  }
  
  .stats-grid {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }
}

@media screen and (max-width: 768px) {
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 576px) {
  .stats-grid {
    grid-template-columns: 1fr;
  }
}
</style> 